.notificationToast {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  background: currentColor;
  border-radius: 100px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: all 0.3s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  -o-transition: all 0.3s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  transition: all 0.3s cubic-bezier(0.770, 0.000, 0.175, 1.000); /* easeInOutQuart */
  /*overflow: hidden;*/
}

.notificationToastClosed {
  composes: notificationToast;
}

.contents {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  white-space: nowrap;
}

.iconContainer {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  width: 62px;
  height: 62px;
  border-radius: 100px;
}

.icon {
  position: relative;
  padding: 6px;
  border-radius: 100px;
}

.img {
  width: 100%;
  height: 100%;
  vertical-align: middle;
}

.ripple {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 100px;
  -webkit-box-shadow: 0 0 0 2px currentColor;
          box-shadow: 0 0 0 2px currentColor;
  -webkit-animation: 'ripple' 1s 0.2s ease-out forwards;
          animation: 'ripple' 1s 0.2s ease-out forwards;
}

.text {
  padding-left: 8px;
  padding-right: 18px;
  font-weight: bold;
  color: black;
  overflow: hidden;
  /*letter-spacing: 1px;*/
  /*transform: translateX(0);*/
  /*transition: all 0.6s ease-out; */
}

.textClosed {
  composes: text;
  /*transform: translateX(15px);*/
}

@-webkit-keyframes :global('ripple') {
  0% { -webkit-transform: scale(0.9); transform: scale(0.9); opacity: 1; }
  100% { -webkit-transform: scale(3); transform: scale(3); opacity: 0; }
}

@keyframes :global('ripple') {
  0% { -webkit-transform: scale(0.9); transform: scale(0.9); opacity: 1; }
  100% { -webkit-transform: scale(3); transform: scale(3); opacity: 0; }
}



