.notifications {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: center;
      justify-content: center;
  width: 800px;
  height: 200px;
  overflow: hidden;
  padding-bottom: 22px;
}

.notificationContainer {
  -webkit-transition: all 0.4s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  -o-transition: all 0.4s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  transition: all 0.4s cubic-bezier(0.770, 0.000, 0.175, 1.000); /* easeInOutQuart */
}

/* ENTER */
.transitionEnter {
  -webkit-transform: translateY(250%);
      -ms-transform: translateY(250%);
          transform: translateY(250%);
  opacity: 0;
}

/* ENTER ACTIVE */
.transitionEnterActive {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

/* LEAVE */
.transitionLeave {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

/* LEAVE ACTIVE */
.transitionLeaveActive {
  -webkit-transform: translateY(250%);
      -ms-transform: translateY(250%);
          transform: translateY(250%);
  opacity: 0;
}

/* APPEAR */
.transitionAppear {}
.transitionAppearActive {}



