.statusBar {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 6px;
  border-radius: 8px;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.transitionGroup {
  display: -ms-flexbox;
  display: flex;
}

.segment {
  /*margin-right: 12px;*/
  -webkit-transition: width 0.4s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  -o-transition: width 0.4s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  transition: width 0.4s cubic-bezier(0.770, 0.000, 0.175, 1.000); /* easeInOutQuart */
}

.segment:last-child {
  margin-right: 0;
}

.item {
  position: relative;
  -webkit-transition: -webkit-transform 0.4s 0.1s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  transition: -webkit-transform 0.4s 0.1s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  -o-transition: transform 0.4s 0.1s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  transition: transform 0.4s 0.1s cubic-bezier(0.770, 0.000, 0.175, 1.000);
  transition: transform 0.4s 0.1s cubic-bezier(0.770, 0.000, 0.175, 1.000), -webkit-transform 0.4s 0.1s cubic-bezier(0.770, 0.000, 0.175, 1.000); /* easeInOutQuart */
}

/* ENTER */
.transitionEnter {
  width: 0;
}

.transitionEnter .item {
  -webkit-transform: translateY(150%);
      -ms-transform: translateY(150%);
          transform: translateY(150%);
}

/* ENTER ACTIVE */
.transitionEnterActive {
  width: 56px;
}

.transitionEnterActive .item {
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
}

/* LEAVE */
.transitionLeave {

}

/* LEAVE ACTIVE */
.transitionLeaveActive {

}

/* APPEAR */
.transitionAppear {}
.transitionAppearActive {}




