.navigationMap {
  background: #151D2A;
  width: 1370px;
  height: 550px;
  position: relative;
  -webkit-perspective: 1500px;
          perspective: 1500px;
  overflow: hidden;
  /*outline: 1px solid red;*/
}

.navigationMap:after {
  content: '';
  position: absolute;
  left: 50%;
  height: 100%;
  border-right: 1px solid red;
  display: none;
}

.map {
  background-image: url('./map_plain-01.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: absolute;
  width: 1086px;
  height: 734px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-57%, -68%) rotateX(50deg) rotateZ(-53deg) scale(1.8);
          transform: translate(-57%, -68%) rotateX(50deg) rotateZ(-53deg) scale(1.8);
}



